body {
  min-width: 320px;
  font-size: 14px;
  line-height: 28px;
  font-family: "Oswald", "Arial", sans-serif;
  color: @mediumgray;
  font-style: normal;
  background-color: @bg-white;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
